.cardHome-container{
    border: solid #E5E8E8  .5px;
    margin-top: 1rem;
    margin-left: .5px;
    margin-right: .5px;
    background-color: white;
    border-radius: 5px;
    box-shadow: 2px 2px 6px 0px rgba(0,0,0,0.20);
}


.cardHome-data-container{
    display: flex;
}
.cardHome-data-info{
    padding: 5px;
}
.cardHome-data-info > :first-child{
    font-weight: 600;
    margin-left: 3px;
    color: #5DADE2;
}
.cardHome-footer-container{
    padding: 7px;
    border-top: solid .3px #E5E8E8;
}
.cardHome-footer-container > :last-child{
    display: flex;
    justify-content: flex-end;
    align-items: center;
}